import React from 'react';
import { View, Text, Button, Image, StyleSheet } from 'react-native';
import { useNavigation } from '@react-navigation/native';
import {CUSTOMON} from './../store/constants';
import GlobalStyles from './../assets/style/GlobalStyles';
const Separator = () => (
    <View style={styles.separator} />
);

type GridProfileProps = {
    onPressFunction:Function,
    accountPage:String,
    logoutPage:String,
    userDetail: {
        name: "",
        email: "",
        avatar_url: ''
    }
}
const UserProfile = ({onPressFunction,accountPage,logoutPage,userDetail}:GridProfileProps) => {
    const navigation = useNavigation();
    const onPressGoToPage = (page:any) => {
        navigation.navigate(page)
        onPressFunction();
    }
    
    return (
        <View>
            {/* Standard Avatar with accessory - Change photo...*/}
            <View style={styles.container}>
                <Image
                    style={styles.avatar}
                    source={{ uri: userDetail.avatar_url }}
                />
                <Text style={{ fontSize: 20, fontWeight: "bold" }}>{userDetail.name}</Text>
                <Text style={{ marginBottom: 15 }}>{userDetail.email}</Text>
                <View style={GlobalStyles.viewButton}>
                    <Button
                        onPress={() => {onPressGoToPage(accountPage)}}
                        title="Gerir conta" 
                        color={CUSTOMON.COLOR}
                        />
                </View>
            </View>
            {/***List App wich user can use */}
            {/*
                list.map((l, i) => (
                    <ListItem key={i} bottomDivider>
                        <Avatar source={{ uri: l.avatar_url }} />
                        <ListItem.Content>
                            <ListItem.Title>{l.name}</ListItem.Title>
                            <ListItem.Subtitle>{l.subtitle}</ListItem.Subtitle>
                        </ListItem.Content>
                    </ListItem>
                ))*/
            }
            <Separator />
            <View style={GlobalStyles.viewButton}>
                <Button
                    onPress={() => {onPressGoToPage(logoutPage)}}
                    title="Sair"
                    color={CUSTOMON.COLOR}
                />
            </View>
        </View>
    )
}

const styles = StyleSheet.create({
    container: {
        justifyContent: "center",
        alignItems: "center",
        marginBottom: 10,
    },
    avatar: {
        width: 150,
        height: 150,
        borderRadius: 150 / 2,
        marginBottom: 15
    },
    separator: {
        marginVertical: 8,
        borderBottomColor: '#737373',
        borderBottomWidth: StyleSheet.hairlineWidth,
    },
});

UserProfile.defaultProps = {
    userDetail: {
        name: "Angelo Correia",
        email: "angelocorreia27@hotmail.com",
        avatar_url: 'data:image/png;base64,'
    }
};
export default UserProfile